.GameApp {
  text-align: center;
}


.GameApp-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.top-bar {
  position: relative;
  top: -150px;
  text-align: center;
}

.start-button {
  height: 75px;
  width: 150px;
  border-radius: 15px;
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.start-button-enabled{
  background-color: red;
  border-color: red;
}

.start-button-disabled{
  background-color: gray;
  border-color: gray;
}

.result {
  color: yellow
}

h2 {
  color: red
}

.enter-user-name {
  position: relative;
  top: -325px;
  left: -85%;
}

.score-rank-header {
  text-decoration: underline;
  color: yellow;
}